<template>
    <div>
      <!-- 查询 -->
      <common-wrapper>
        <main-management-search class="search"
        :warehouseList="warehouseList"
        :merchantsList="merchantsList"
        :modelBrand="modelBrand"
        :brandList="brandList"
        :allModelList="modelList"
        @getMerchants="getMerchantsList"
        @getBrandModel="getBrandModel"
        @search="queryList" @rest="rest" />
      </common-wrapper>
      <!-- <header-breadcrumb :breadData="$route.matched" /> -->
      <nav-button>
        <el-button-group>
           <el-button icon="fdddfont icon-shujudaoru"  @click="handleExport">导出交易明细</el-button>
        </el-button-group>
      </nav-button>

     <!-- 交易明细列表 -->
      <common-wrapper>
        <common-table :flex="290">
          <template slot="table">
            <el-table
              ref="userTable"
              height="100%"
              stripe
              :border="true"
              tooltip-effect="light"
              v-loading="isListLoading"
              :data="tableData"
              @row-dblclick="getUserDetail"
              @selection-change="handleSelectionChange">
              <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
              <el-table-column prop="merchants" label="商户" min-width="120" sortable></el-table-column>
                <el-table-column prop="band" label="品牌" min-width="120" sortable></el-table-column>
                <el-table-column prop="productTempleteName" label="货品名称" min-width="120" sortable ></el-table-column>
                <el-table-column prop="name" label="子件名" min-width="120" :show-overflow-tooltip="true" ></el-table-column>
                <el-table-column prop="model" label="型号" min-width="100" ></el-table-column>
                <el-table-column prop="color" label="颜色" width="100"></el-table-column>
                <el-table-column prop="standard" label="规格" min-width="100" ></el-table-column>
                <!-- <el-table-column prop="maintype" label="货品类型" :formatter="formatType" >

                </el-table-column> -->
                <el-table-column prop="maintype" label="货品类型">
                    <template slot-scope="scope">
                      <el-tooltip v-if="scope.row.maintype == '0'" class="item" effect="light" content="查看小件出库明细"  placement="bottom">
                        <el-button  type="text" @click="getUserDetail(scope.row)">{{formatType(scope.row)}}</el-button>
                      </el-tooltip>
                      <span v-else>{{formatType(scope.row)}}</span>
                    </template>
                  </el-table-column>
                <el-table-column prop="entryTime" label="入库时间" sortable min-width="150" :formatter="formatEntry"></el-table-column>
                <el-table-column prop="launchTime" label="上架时间" sortable min-width="150" :formatter="formatLaunch"></el-table-column>
                <el-table-column prop="exportTime" label="出库时间" sortable min-width="150" :formatter="formatExport"></el-table-column>
                <el-table-column prop="exportNumber" label="出库单号" sortable min-width="180" :show-tooltip-when-overflow="true"></el-table-column>
                <el-table-column label="操作" width="60" fixed="right">
                  <template slot-scope="scope">
                    <el-tooltip class="item" v-if="scope.row.maintype == '0'" effect="light" content="查看小件出库明细"  placement="bottom">
                      <el-button size="mini" round icon="el-icon-view" @click.stop="getUserDetail(scope.row,scope.$index)"></el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>
      <main-detail
            :show.sync="isDetailVisible"
            :searchParams="searchParams"
            :queryInfo.sync="queryInfo"
      />
    </div>
</template>

<script>
/**
 *  transactionDetail Management Module
 * ----------------------
 * Author: Calamus
 * Date: 2018.09.05
 */
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import MainManagementSearch from './Components/Search'
import MainDetail from './Components/detail'

// API
import { getAllOrgList, getBrandModel, getAllMerchantsList } from '@/api/common'
import { getWarehouseList, transactionList, transactionExport } from '@/api/warehouse'
import { setSession, getSession } from '@/utils/session'

export default {
  name: 'transactionDetail',
  mixins: [mixins],
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    MainManagementSearch,
    MainDetail
  },
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: [], // 列表选中列
      choosedIds: [],
      isListLoading: false, // 列表加载
      isDetailVisible:false,
      warehouseList: this.$store.getters.warehouseList,
      merchantsList: [], // 公用商户列表
      brandList: [], // 商户列表
      modelList: [],
      modelBrand: [],
      isCreateFormVisible: false, // 新增界面是否显示
      isEditFormVisible: false, // 编辑界面是否显示
      isRoleAuthVisible: false, // 用户角色授权窗口是否展示
      modifyUser: null, // 弹出框编辑时存放的临时行数据
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {}, // 查询参数
      queryInfo:null

    }
  },
  activated () {
    this.$refs.userTable.doLayout()
  },
  mounted () {
    this.getMerchantsList()
    this.getBrandModel({warehouseUuid: getSession('warehouseUuid')})
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getUsers(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 获取品牌型号列表
    getBrandModel (params) {
      getBrandModel(params).then(res => {
        if (!res.error) {
          this.brandList = []
          this.modelList = []
          res.forEach((item, index) => {
            this.brandList.push(item.productBrand)
            item.productModel.forEach(item2 => {
              this.modelList.push(item2)
            })
          })
          this.modelBrand = res
        }
      })
    },
    // 商户列表
    getMerchantsList (params = {warehouseUuid: getSession('warehouseUuid') ? getSession('warehouseUuid') : this.warehouseList[0].warehouseUuid, userUuid: ''}) {
      return new Promise(resolve => {
        getAllMerchantsList(params).then(res => {
          if (!res.error) {
            this.merchantsList = res
            resolve()
          }
        })
      })
    },
    // 获取列表
    getUsers (params) {
      this.isListLoading = true
      transactionList(params).then(res => {
        if(!res.error){
          if(res.item.length == 0 && res.pageNum > 1){
            this.pagination.pageNum = 1
            params.pageNum = 1
            this.getUsers(params)
            return
          }
          this.tableData = res.item
          this.pagination.total = res.total
          this.pagination.pageNum = res.pageNum
          this.pagination.pageSize = res.pageSize
          this.isListLoading = false
        }
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getUsers(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    handleSelectionChange (item) {
      this.selectRow = item
      let oIds = []
      this.choosedIds = this.selectRow.map(row => oIds.push(row.id)).join(',')
    },
    isButtonIsDisabled () {
      return !!this.selectRow.length
    },
    handleExport () {
      let params = this.searchParams
      transactionExport(params).then((res) => { // 处理返回的文件流
        const content = res.data
        const blob = new Blob([content], {type: 'application/ms-excel'})
        const fileName = '交易明细' + moment(new Date()).format('YYYYMMDD') + '.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      })
    },
    isButtonDisabled () {
      return !!this.selectRow.length
    },
    // 查询详情
    getUserDetail (row, index) {
      console.log("click",row,"index",index)
      if(row.maintype == '0'){
        this.queryInfo = JSON.parse(JSON.stringify(row))
        this.isDetailVisible = true
      }
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 列表状态显示格式化
    formatStatus (row, column) {
      return row.userEnable === '1' ? '启用' : row.userEnable === '0' ? '禁用' : '未知'
    },
    formatType (row) {
      if (row.maintype) {
        return row.maintype == '0' ? '小件' : row.maintype == '1' ? '套件' : row.maintype == '2' ? '单品' : '未知'
      }
    },
    // 列表日期格式化
    formatEntry (row) {
      if (row.entryTime) {
        return moment(row.entryTime).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
      } else {
        return ''
      }
    },
    formatLaunch (row) {
      if (row.launchTime) {
        return moment(row.launchTime).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
      } else {
        return ''
      }
    },
    formatExport (row) {
      if (row.exportTime) {
        return moment(row.exportTime).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
      } else {
        return ''
      }
    }

  }
}
</script>



