<template>
     <el-form :model="searchForm" inline ref="searchForm"  label-width="80px" >
                  <el-form-item label="仓库" prop="warehouseUuid">
                    <el-select  filterable placeholder="请选择仓库" v-model="searchForm.warehouseUuid" @change="getMerchantsList" >
                        <el-option
                          v-for="(item, index) in warehouseList"
                          :key="index"
                          :label="item.warehouseName"
                          :value="item.warehouseUuid"
                        >{{item.warehouseName}}</el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="商户" prop="merchantUuid">
                    <el-select  placeholder="请选择商户" filterable clearable v-model="searchForm.merchantUuid" @change="getBrand">
                        <el-option
                          v-for="(item, index) in merchantsList"
                          :key="index"
                          :label="item.userName"
                          :value="item.userUuid"
                        >{{item.userName}}</el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="品牌" prop="productBrand">
                    <el-select  placeholder="请选择品牌" @change="brandChange" filterable clearable v-model="searchForm.productBrand">
                        <el-option
                          v-for="(item, index) in brandList"
                          :key="index"
                          :label="item"
                          :value="item"
                        >{{item}}</el-option>
                    </el-select>
                  </el-form-item>

                    <el-form-item label="型号"  prop="productModelName">
                      <el-select  v-model="searchForm.productModelName" filterable clearable placeholder="请选择型号">
                        <el-option
                          v-for="(item,index) in modelList"
                          :key="index" class="radio"
                          :value="item"
                          :label="item">{{item}}</el-option>
                      </el-select>
                    </el-form-item>
       <el-form-item label="货品名称"  prop="productTempleteName" label-width="80px">
         <el-input v-model="searchForm.productTempleteName"  maxlength="200"  clearable placeholder="请输入货品名称"></el-input>
       </el-form-item>
       <el-form-item label="颜色"  prop="color" label-width="80px">
         <el-input v-model="searchForm.color"  maxlength="200"  clearable placeholder="请输入货品颜色"></el-input>
       </el-form-item>
       <el-form-item label="规格"  prop="standard" label-width="80px">
         <el-input v-model="searchForm.standard"  maxlength="200"  clearable placeholder="请输入货品规格"></el-input>
       </el-form-item>
                    <!--<el-form-item label="货品类型" prop="productType">-->
                      <!--<el-select  placeholder="请选择货品类型"  filterable clearable v-model="searchForm.productType" >-->
                          <!--<el-option label="小件" value="0"/>-->
                          <!--<el-option label="套件" value="1"/>-->
                          <!--<el-option label="单品" value="2"/>-->
                      <!--</el-select>-->
                    <!--</el-form-item>-->
       <el-form-item label="入库时间"  prop="beginDate">
         <el-date-picker
           style="width: 173px;"
           v-model="searchForm.beginDate"
           type="date"
           align="right"
           unlink-panels
           range-separator="至"
           placeholder="请选择入库时间"
           format="yyyy 年 MM 月 dd 日"
           value-format="yyyy-MM-dd"
           size="mini"
           clearable>
         </el-date-picker>
       </el-form-item>
       <el-form-item label="出库时间"  prop="endDate">
         <el-date-picker
           style="width: 173px;"
           v-model="searchForm.endDate"
           type="date"
           align="right"
           unlink-panels
           range-separator="至"
           placeholder="请选择出库时间"
           format="yyyy 年 MM 月 dd 日"
           value-format="yyyy-MM-dd"
           size="mini"
           clearable>
         </el-date-picker>
       </el-form-item>
       <span>
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
       </span>
          </el-form>
</template>

<script>
/**
 * Maintenance Management Module
 * ----------------------
 * Author: Calamus
 * Date: 2018.08.29
 */
import { setSession, getSession } from '@/utils/session'
import moment from 'moment'


export default {
  name: 'MainManagementSearch',
  data () {
    return {
      // 检索界面数据
      searchForm: {
        inventoryUuid:'',
        warehouseUuid: '',
        productTempleteUuid:'',
        standard:'',
        color:'',
        productModelName:'',
        productTempleteName:'',
        productType:'',
        brandUuid:'',
        storageUuid:'',
        createUserUuid:'',
        createTimeBegin:'',
        createTimeEnd:'',
        merchantUuid:'',
        beginDate:'',
        endDate:'',
        timeRange:[],
      },
      modelList: []
    }
  },
  mounted () {
    this.searchForm.warehouseUuid = getSession('warehouseUuid')
    this.search()
  },
  props: {
    warehouseList:{
      type: Array,
      required: true
    },
    merchantsList:{
      type: Array,
      required: true
    },
    brandList: {
      type: Array
    },
    allModelList: {
      type: Array
    },
    modelBrand: {
      type: Array
    }
  },
  watch:{
    allModelList(val) {
      this.modelList = Object.assign({},val)
    }
  },
  methods: {
    getMerchantsList (value) {
      setSession('warehouseUuid',value)
      this.searchForm.productBrand = ''
      this.searchForm.merchantUuid = ''
      this.searchForm.productModelName = ''
      this.$emit('getBrandModel',Object.assign({},this.searchForm))
      this.$emit('getMerchants',Object.assign({},this.searchForm))
      },
    getBrand () {
      this.searchForm.productBrand = ''
      this.searchForm.productModelName = ''
      this.$emit('getBrandModel',Object.assign({},this.searchForm))
      },
    // 获取型号
    brandChange (value) {
      this.searchForm.productModelName = ''
      if (value === '') {
        this.modelList = Object.assign({},this.allModelList)
      } else {
        let model = []
        model = this.modelBrand.find(item => {
          return item.productBrand === value
        })
        this.modelList = model.productModel
      }
    },
    // 检索
    search: function () {
      this.$emit('search', Object.assign({}, this.searchForm))
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



