<template>
  <el-dialog
      width="800px"
      title="小件出库明细"
      :visible.sync="isVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :append-to-body="true"
      :lock-scroll="true"
      @close="cancel"
      center
      >
      <div>
        <el-table
              ref="userTable"
              height="100%"
              stripe
              :border="true"
              tooltip-effect="light"
              v-loading="isListLoading"
              :data="tableData">
                <el-table-column prop="deliverOrderUuid" label="出库单号" min-width="180" ></el-table-column>
                <el-table-column prop="exportTime" label="出库时间" :formatter="formatDate" min-width="120" sortable></el-table-column>
                <el-table-column prop="productDeliverCount" label="出库数量" min-width="120"  ></el-table-column>
                <el-table-column prop="pickUserName" label="拣货人" min-width="120" :show-overflow-tooltip="true" ></el-table-column>
                <el-table-column prop="deliverUserName" label="发货人" min-width="120" :show-overflow-tooltip="true" ></el-table-column>
            </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
      </span>
  </el-dialog>
</template>

<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import NavButton from '@/components/navButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import { setSession, getSession } from '@/utils/session'
import { getPieceDetail } from '@/api/warehouse'

export default {
  name: 'CustomerQuery',
  components: {
    VuePerfectScrollbar, NavButton, CommonWrapper, CommonTable
  },
  props:{
    queryInfo:{
      type:Object
    },
    show: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isVisible:false,
      tableData:[],
      isListLoading:false
    }
  },
  watch:{
    show (isShow) {
          this.isVisible = isShow
          if(isShow){
            this.getDetail()
          }
        }
  },
  mounted () {
  },
  methods: {
      getDetail(){
        this.isListLoading = true
        getPieceDetail({id:this.queryInfo.id}).then(res=>{
          if(!res.error){
            this.tableData = res
            this.isListLoading = false
          }
        })
      },
      formatDate (row,col) {
        if (row[col.property]) {
          return moment(row[col.property]).format('YYYY-MM-DD')
        }
      },
      cancel(){
        this.$emit('update:show', false)
        this.$emit('update:queryInfo', null)
        this.$emit('updateList')
        this.isVisible = false
      }
  },
}
</script>

<style scoped>
  .el-radio{
    margin-right: 30px !important;
    margin-left:0px !important;
  }
</style>



